import React, { Component } from "react";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import Table from "react-bootstrap/Table";

class Player extends Component {
  constructor(props) {
    super(props);
    this.state = {
      playerName: null,
      playerInfo: {},
      playerStats: {},
      temporaryDisplayName: null,
    };
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.getPlayerId();
    this.getPlayerInfo();
    console.log(this.state.playerName);
  };

  handleChange = (event) => {
    //This is going to be responsible for replacing blank spaces
    //with underscores
    const replace = event.target.value.split(" ").join("_");
    if (replace.length > 0) {
      this.setState({ playerName: replace });
    } else {
      alert("Please type players Name");
    }
  };

  getPlayerId = () => {
    axios
      .get(
        `https://www.balldontlie.io/api/v1/players?search=${this.state.playerName}`
      )
      .then(async (res) => {
        console.log(res.data.data);
        if (res.data.data[0] === undefined) {
          alert("This player is either injured or hasn't played yet");
        } else if (res.data.data > 1) {
          alert("please specify the name more!");
        } else {
          //this is to get player Id
          await this.getPlayerStats(res.data.data[0].id);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  getPlayerStats = (playerId) => {
    axios
      .get(
        `https://www.balldontlie.io/api/v1/season_averages?season=2020&player_ids[]=${playerId}`
      )
      .then(async (res) => {
        console.log(res.data.data);
        //This is to get the players stats
        this.setState({ playerStats: res.data.data[0] });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  getPlayerInfo = () => {
    axios
      .get(
        `https://www.balldontlie.io/api/v1/players?search=${this.state.playerName}`
      )
      .then(async (res) => {
        if (res.data.data[0] === undefined) {
          alert("This player is either injured or hasn't played yet");
        } else if (res.data.data > 1) {
          alert("please specify the name more!");
        } else {
          this.setState({ playerInfo: res.data.data[0] }) &&
            this.setState({
              temporaryDisplayName:
                this.state.playerInfo["last_name"] +
                ", " +
                this.state.playerInfo["first_name"],
            });
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  getPhotoId = () => {
    axios
      .get(`http://data.nba.net/data/10s/prod/v1/2019/players.json/`)
      .then(async (res) => {
        console.log(res.data.league.standard);

        // eslint-disable-next-line no-lone-blocks
      })
      .catch((err) => {
        console.log(err);
      });
  };

  componentDidMount() {
    this.getPlayerStats();
    this.getPhotoId();
  }
  render() {
    return (
      <div className="App">
        <form onSubmit={this.handleSubmit}>
          <label>
            Search For A Player
            <input
              type="text"
              value={this.state.value}
              onChange={this.handleChange}
              placeholder="Enter players name"
            />
          </label>
          <input type="submit" value="Submit" />
          <br />
        </form>
        <br />
        <h1>
          {this.state.playerInfo["first_name"]}{" "}
          {this.state.playerInfo["last_name"]}
        </h1>
        <Table striped bordered hover variant="dark">
          <thead>
            <tr>
              <th>Year</th>
              <th>Games Played</th>
              <th>Points</th>
              <th>FG%</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>2020</td>
              <td>{this.state.playerStats["games_played"]}</td>
              <td>{this.state.playerStats["pts"]}</td>
              <td>
                {this.state.playerStats["fg_pct"]}
                {"%"}
              </td>
            </tr>
          </tbody>
        </Table>
      </div>
    );
  }
}

export default Player;
